.intro-section {
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: end;
  -ms-flex-align: end;
  align-items: center;
  position: relative;
  z-index: 3;
  color: #fff;
}
.intro-section:after {
  min-height: 100vh;
  display: block;
  width: 0;
  content: '';
}

.intro-section:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(17, 29, 52, 0.2);
  z-index: 1;
}
.intro-section.purple-gradient:before {
  background-image: -webkit-linear-gradient(45deg, #9c2ef0 0%, #4d90ff 100%);
  background-image: linear-gradient(45deg, #9c2ef0 0%, #4d90ff 100%);
}
.intro-section.small-intro {
  padding: 75px 0;
}
.intro-section.small-intro:after {
  min-height: 320px !important;
}
.intro-section > * {
  z-index: 3;
}
.intro-section .btn-down {
  position: absolute;
  top: 100%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  top: calc(100% - 35px);
  z-index: 5;
}

.intro-section.overlay:before {
  background-color: rgba(28, 42, 70, 0.6);
}
.intro-section.gradient-overlay:before {
  background-image: -webkit-linear-gradient(45deg, #9c2ef0 0%, #4d90ff 100%);
  background-image: linear-gradient(45deg, #9c2ef0 0%, #4d90ff 100%);
  opacity: 0.3;
}
.intro-section.center {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.intro-section.alt .intro-content {
  max-width: 100%;
  margin-top: 68px;
  font-size: 17px;
}
.intro-section.research-intro {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.intro-section.research-intro .intro-content {
  padding: 0;
}
.intro-section.research-intro .intro-title {
  font-weight: 400;
  max-width: 160px;
  font-size: 20px;
  margin: 0 auto;
}
.intro-section.research-intro:after {
  min-height: 250px;
}
.intro-section.developers-intro:before {
  display: none;
}
.intro-section.developers-intro:after {
  min-height: 182px;
}
.intro-section.developers-intro .intro-content .intro-title {
  font-size: 14px;
  line-height: 1.14285714;
}
.intro-content {
  padding: 140px 0 80px;
  text-align: center;
  font-size: 15px;
  line-height: 1.27;
  max-width: 500px;
  margin: 0 auto;
}

.intro-content .intro-title {
  display: block;
  font-size: 33px;
  line-height: 1.36;
  font-weight: 600;
  margin-bottom: 6px;
}
.intro-content .intro-title span:not([class]) {
  display: block;
  font-weight: 300;
  font-size: 20px;
  line-height: 1;
}
#cd-vertical-nav {
  position: fixed;
  right: 10px;
  top: 50%;
  bottom: auto;
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  transform: translateY(-50%);
  z-index: 40;
}
#cd-vertical-nav ul {
  list-style: none;
  padding: 0;
}
#cd-vertical-nav li {
  text-align: right;
}
#cd-vertical-nav a {
  display: inline-block;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  width: 100%;
}
#cd-vertical-nav a:hover span,
#cd-vertical-nav a.is-selected .cd-dot {
  -webkit-transform: scale(1);
  -moz-transform: scale(1);
  -ms-transform: scale(1);
  -o-transform: scale(1);
  transform: scale(1);
}

#cd-vertical-nav .cd-dot {
  position: relative;
  top: 8px;
  right: calc(100% - 15px);
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-color: #e64c39;
  -webkit-transition: -webkit-transform 0.2s, background-color 0.5s;
  -moz-transition: -moz-transform 0.2s, background-color 0.5s;
  transition: transform 0.2s, background-color 0.5s;
  -webkit-transform-origin: 50% 50%;
  -moz-transform-origin: 50% 50%;
  -ms-transform-origin: 50% 50%;
  -o-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
}
#cd-vertical-nav a span {
  float: right;
  display: inline-block;
  -webkit-transform: scale(0.6);
  -moz-transform: scale(0.6);
  -ms-transform: scale(0.6);
  -o-transform: scale(0.6);
  transform: scale(0.6);
}
#cd-vertical-nav .cd-label {
  position: relative;
  margin-right: 10px;
  padding: 4px 14px;
  color: white;
  background: rgba(0, 0, 0, 0.53);
  font-size: 10px;
  border-radius: 20px;
  text-transform: uppercase;
  font-weight: 600;
  opacity: 0;
  -webkit-transform-origin: 100% 50%;
  -moz-transform-origin: 100% 50%;
  -ms-transform-origin: 100% 50%;
  -o-transform-origin: 100% 50%;
  transform-origin: 100% 50%;
}
#cd-vertical-nav a:after {
  content: '';
  display: table;
  clear: both;
}
#cd-vertical-nav a:hover .cd-label {
  opacity: 1;
  left: -100%;
  -webkit-transition: -webkit-transform 0.2s, opacity 0.2s;
  -moz-transition: -moz-transform 0.2s, opacity 0.2s;
  transition: transform 0.2s, opacity 0.2s;
}
.ph {
  color: red;
  font-weight: bold;
}
